<template>
    <div> 
        <Fztest :taskCont="mydata" @Csend="fatherSend"></Fztest>
        <!-- 父子传值 -->
        <test><div slot="one" slot-scope="kk">{{kk}}</div></test>
    </div>
</template>
<script>
import test from '@/components/test.vue'
import Fztest from '@/components/fztest.vue'
export default {
    data(){
        return{
          mydata:"kkkkkk"  
        }
    },
    components:{
        Fztest,
        test
    },
    methods:{
        fatherSend(data){
            this.mydata=data
        }
    }

}
</script>



