<template>
    <div>
          我是插槽
          <slot name="one"  :say="chacao"></slot>
          <slot name="two"></slot>
    </div>
</template>
<script>
export default {
    data(){
        return{
            chacao:"我是来自作用于"
        }
    }
}
</script>




